*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    font-family: 'Ubuntu', sans-serif;
    background: url(./Archivos/Imagenes/fondo1blackout.png);
}
.containertodo{
    margin-top: -400px;
    display: flex;
    flex-direction: column;
    align-items: center;
     justify-content: center;
}
.navbar{
    border-radius: 50px;
    width: 100px;
    height: 100px;
   display: flex;
   align-items: center;
    justify-content: center;
}
.logo1{
    width: 100px;
    
   
}
.navbar:hover{
    background-color: violet;
   padding: 2px;
   
}

.container{
    margin-top: 100px;
    width: 250px;
    height: auto;
    text-align: center;
    background-color: white;
    border-radius: 3px;

}
.container h1{
    text-align: left;
    padding: 10px;
    font-size: 16px;
    background-color: #eee;
    color: #333;
    letter-spacing: 3px;

}
.container ul li{
    list-style: none;
}
.container ul li a{
    display: block;
    padding: 10px 0;
    text-decoration: none;
    font-weight: 700;
    color: #333;
    background-color: white;
    font-size: 18px;
    text-transform: uppercase;
    transition: .1s;
    border: 1px solid #eee;
}
.container:hover ul li a{
    color: rgb(185, 180, 180);
}

.container ul li a:hover{
    color: rgb(0, 255, 242);
    transform: scale(1.1);
    background-color: violet;
    border: 1px solid violet;
}

.container ul li a:hover::before{
    content: '';
    width: 0;
    height: 0;
    position: absolute;
    left: -1.5px;
    top: -12.5px;
    border-style: solid ;
    border-width: 0 0 11.5px 11.5px;
    border-color: transparent transparent rgb(240, 114, 240) transparent;
}

.container ul li a:hover::after{
    content: '';
    width: 0;
    height: 0;
    position: absolute;
    right: -1.5px;
    top: -12.5px;
    border-style: solid ;
    border-width: 11.5px 0 0 11.5px;
    border-color: transparent transparent transparent rgb(240, 114, 240);
}